.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl{
	padding-right: 12px;
    padding-left: 12px;
}
.input-group-lg>.custom-select, .input-group-lg>.form-control, .input-group-lg>.input-group-append>.btn, .input-group-lg>.input-group-append>.input-group-text, .input-group-lg>.input-group-prepend>.btn, .input-group-lg>.input-group-prepend>.input-group-text{
	font-size: .875rem;
}
.input-group-text{
	background-color: #f5f7fb;
}
.input-group-lg>.custom-select, .input-group-lg>.form-control:not(textarea){
	height: calc(1.5em + 1rem + 6px);
}
.bg-soft-light{
	background-color: rgba(230,235,245,.25)!important;
}
.custom-control-input:checked~.custom-control-label::before{
	border-color: #7269ef;
    background-color: #7269ef;
}
.custom-control-input:focus~.custom-control-label::before{
	box-shadow: none;
}
.custom-control-input:focus:not(:checked)~.custom-control-label:before{
    border-color: #c2cee6;
}
.custom-control-input:not(:disabled):active~.custom-control-label:before{
	color: #fff;
    background-color: rgba(114,105,239,.1);
    border-color: rgba(114,105,239,.1);
}
.btn-primary{
	color: #fff;
    background-color: #7269ef;
    border-color: #7269ef;
}
.btn-primary.focus, .btn-primary:focus, .btn-primary:hover{
	color: #fff;
    background-color: #5246eb;
    border-color: #473bea;
    box-shadow: none;
}
.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle{
	color: #fff;
    background-color: #473bea;
    border-color: #3c2fe9;
}
.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus{
	box-shadow: 0 0 0 0.15rem rgba(135,128,241,.5);
}
.btn{
	padding: .47rem .75rem;
    font-size: .9375rem;
}
.custom-control-input{
	height: 1.20313rem;
}
.custom-control{
	min-height: 1.40625rem;
}
.custom-control-label::before{
    top: .20313rem;
}
.text-primary{
	color: #7269ef!important;
}
a.text-primary:focus, a.text-primary:hover{
	color: #3124e8!important;
}
a:hover{
	text-decoration: none;
}
.text-danger{
    color: #ef476f!important;
}
.form-control:focus{
	box-shadow: none;
}